// ===================== components/product/ProductDetail.jsx =====================
import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { streetFoods, productReviews } from "../../services/productService";
import {
  ShoppingCart,
  Star,
  CheckCircle,
  Clock,
  ChefHat,
  Heart,
} from "lucide-react";
import { useCart } from "../../context/CartContext";

// Local CSS
import "./productdetail.css";

export default function ProductDetail() {
  const { id } = useParams();
  const navigate = useNavigate();

  const pid = Number(id);
  const product = streetFoods.find((p) => p.id === pid);

  const cartCtx = useCart?.() || {};
  const {
    addToCart,
    add,
    wishlist = [],
    toggleWishlist,
    toggleWish,
  } = cartCtx;

  const handleAdd = addToCart || add;
  const handleToggleWish = toggleWishlist || toggleWish;

  // Reviews state
  const [reviews, setReviews] = useState(productReviews?.[pid] ?? []);
  const [newRating, setNewRating] = useState(0);
  const [newComment, setNewComment] = useState("");

  useEffect(() => {
    setReviews(productReviews?.[pid] ?? []);
    setNewRating(0);
    setNewComment("");
  }, [pid]);

  if (!product) {
    return (
      <div className="max-w-7xl mx-auto px-4 py-16">
        <button
          onClick={() => navigate(-1)}
          className="back-home-btn mb-6"
        >
          ← Back to Menu
        </button>
        <div className="text-xl font-semibold">Product not found.</div>
      </div>
    );
  }

  const wished = Array.isArray(wishlist) && wishlist.includes(product.id);
  const savings =
    typeof product.originalPrice === "number" &&
    product.originalPrice > product.price
      ? product.originalPrice - product.price
      : 0;

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Back button with new style */}
      <button
        onClick={() => navigate(-1)}
        className="back-home-btn mb-6"
      >
        ← Back to Menu
      </button>

      <div className="pd-wrap">
        {/* Left: media */}
        <div className="pd-media relative">
          <img src={product.image} alt={product.name} />
          <div className="absolute top-4 left-4 flex flex-col gap-2">
            {product.isBestseller && (
              <span className="pill pill-red">🔥 BESTSELLER</span>
            )}
            {product.discount > 0 && (
              <span className="badge-green">{product.discount}% OFF</span>
            )}
          </div>
        </div>

        {/* Right: info */}
        <div className="pd-card space-y-6">
          <h1 className="pd-title text-3xl lg:text-4xl">{product.name}</h1>

          {/* Meta */}
          <div className="pd-meta">
            <div className="flex items-center gap-1">
              <Star className="text-yellow-400" size={20} fill="currentColor" />
              <span className="font-medium">{product.rating}</span>
              <span className="text-slate-500">
                ({product.reviewCount} reviews)
              </span>
            </div>

            <div className="flex items-center gap-2">
              <CheckCircle className="text-green-600" size={16} />
              <span className="text-green-600 text-sm">Pure Veg</span>
            </div>
          </div>

          <p className="text-slate-600 leading-relaxed">
            {product.description}
          </p>

          {/* Price */}
          <div className="price-row">
            <span className="price">₹{product.price}</span>
            {product.originalPrice > product.price && (
              <span className="strike">₹{product.originalPrice}</span>
            )}
            {savings > 0 && <span className="save-pill">Save ₹{savings}</span>}
          </div>

          {/* Stats + Nutrition */}
          <div className="grid grid-cols-2 gap-6">
            <div className="space-y-3">
              <div className="stat">
                <Clock className="text-slate-400" />
                <div>
                  <div className="label">Prep Time</div>
                  <div className="sub">{product.prepTime}</div>
                </div>
              </div>

              <div className="stat">
                <ChefHat className="text-slate-400" />
                <div>
                  <div className="label">Spice Level</div>
                  <div
                    className={`font-semibold ${
                      product.spiceLevel === "Mild"
                        ? "text-green-600"
                        : product.spiceLevel === "Medium"
                        ? "text-amber-600"
                        : "text-red-600"
                    }`}
                  >
                    🌶️ {product.spiceLevel}
                  </div>
                </div>
              </div>
            </div>

            <div className="nutri-card">
              <div className="nutri-title">Nutrition (per serving)</div>
              <div className="nutri-grid text-sm">
                <div>
                  <small>Calories</small> • {product.nutritionInfo?.calories ?? "-"}
                </div>
                <div>
                  <small>Protein</small> • {product.nutritionInfo?.protein ?? "-"}g
                </div>
                <div>
                  <small>Carbs</small> • {product.nutritionInfo?.carbs ?? "-"}g
                </div>
                <div>
                  <small>Fat</small> • {product.nutritionInfo?.fat ?? "-"}g
                </div>
              </div>
            </div>
          </div>

          {/* Actions */}
          <div className="actions">
            <button
              onClick={() => handleAdd && handleAdd(product)}
              className="btn-primary flex-1"
              disabled={!handleAdd}
            >
              <ShoppingCart />
              <span>Add to Cart — ₹{product.price}</span>
            </button>

            <button
              onClick={() => handleToggleWish && handleToggleWish(product.id)}
              className={`wish-btn px-5 py-4 rounded-lg border-2 ${
                wished
                  ? "border-red-500 bg-red-50 text-red-600"
                  : "border-slate-300 hover:border-red-500 hover:text-red-600"
              }`}
              aria-pressed={wished}
              disabled={!handleToggleWish}
              title={wished ? "Remove from wishlist" : "Add to wishlist"}
            >
              <Heart size={20} fill={wished ? "currentColor" : "none"} />
            </button>
          </div>

          {/* Reviews */}
          <div className="reviews mt-12 p-6 lg:p-8">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold">Customer Reviews</h2>
              <span className="text-sm text-slate-500">({reviews.length})</span>
            </div>

            {/* Add review form */}
            <div className="mb-8 border rounded-lg p-4 review-form">
              <div className="add-rating-title">Add your rating</div>
              <div className="flex gap-1 mb-4">
                {[...Array(5)].map((_, i) => (
                  <Star
                    key={i}
                    size={20}
                    onClick={() => setNewRating(i + 1)}
                    className={
                      i < newRating
                        ? "star-interactive text-yellow-400 fill-current"
                        : "star-interactive text-slate-300"
                    }
                    aria-label={`Rate ${i + 1} star`}
                    role="button"
                    tabIndex={0}
                    onKeyDown={(e) =>
                      (e.key === "Enter" || e.key === " ") && setNewRating(i + 1)
                    }
                  />
                ))}
              </div>

              <textarea
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
                placeholder="Share your experience..."
                className="review-textarea"
              />

              <button
                onClick={() => {
                  if (!newRating) {
                    alert("कृपया रेटिंग चुनें");
                    return;
                  }
                  const newReview = {
                    id: Date.now(),
                    user: "Guest",
                    rating: newRating,
                    comment: newComment || "—",
                  };
                  setReviews((prev) => [...prev, newReview]);
                  setNewRating(0);
                  setNewComment("");
                }}
                className="submit-review-btn"
              >
                Submit Review
              </button>
            </div>

            {/* Existing reviews list */}
            {reviews.length ? (
              <div className="space-y-6">
                {reviews.map((r) => (
                  <div key={r.id} className="border-b pb-6">
                    <div className="flex items-center justify-between mb-2">
                      <div className="font-medium">{r.user}</div>
                      <div className="flex items-center gap-1">
                        {[...Array(5)].map((_, i) => (
                          <Star
                            key={i}
                            size={16}
                            className={
                              i < r.rating
                                ? "text-yellow-400 fill-current star-compact"
                                : "text-slate-300 star-compact"
                            }
                          />
                        ))}
                      </div>
                    </div>
                    <p className="text-slate-700">{r.comment}</p>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-8 text-slate-500">
                No reviews yet.
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
